<template>
    <div id="plan" style="transform:translateX(100%);transition:.3s;">
        <mt-header title="" style="height:0.88rem;background: #4cc494;font-size:0.38rem;">
            <router-link to="/" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
        <div>
            <div style="display:flex;line-height:1rem;font-size:0.3rem;text-align:center;">
                <div v-for="(i,$index) in word_arr" style="flex:1;" @click="active=i.path" :class="active==i.path?'word_active':''">
                    {{i.name}}
                </div>
            </div>
            <mt-tab-container v-model="active" swipeable>
                <mt-tab-container-item id="tab-container1" style="overflow:hidden;min-height:90vh;">
                    <div v-for="i in cont_arr" v-if="i.type==''" class="plan_box">
                        <div>
                            <p>{{i.name}}</p>
                            <p>{{i.num}}</p>
                        </div>
                    </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="tab-container2" style="overflow:hidden;min-height:90vh;">
                    <div v-for="i in cont_arr" v-if="i.type==''" class="plan_box">
                        <div>
                            <p>{{i.name}}</p>
                            <p>{{i.num}}</p>
                        </div>
                    </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="tab-container3" style="overflow:hidden;min-height:80vh;">
                    <div v-for="i in cont_arr" v-if="i.type==''" class="plan_box">
                        <div>
                            <p>{{i.name}}</p>
                            <p>{{i.num}}</p>
                        </div>
                    </div>
                </mt-tab-container-item>
            </mt-tab-container>
        </div>
    </div>
</template>

